import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack} from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider,   RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
System.propTypes = {
    discount: PropTypes.object,
  };
export default function System({ discount}) {
    const { enqueueSnackbar } = useSnackbar();
    const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
app_name: Yup.string().required(),
app_phone: Yup.string().required(),
app_address: Yup.string().required(),
app_email: Yup.string().required().email(),
facebook: Yup.string().required(),
tiktok: Yup.string().required(),
instagram: Yup.string().required()
 });

  const defaultValues = useMemo(
    () => ({
    app_name: discount?.app_name,
    app_phone: discount?.app_phone,
    app_address: discount?.app_address,
    app_email: discount?.app_email,
    facebook: discount?.facebook,
    instagram: discount?.instagram,
    tiktok: discount?.tiktok
    }),
    [discount]
  );
  const AccessToken =  window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount!== undefined) {
      reset(defaultValues);
    }
   
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]); 
  const {
    reset,
    setError,
    handleSubmit,
    formState: {errors, isSubmitting },
  } = methods;
 
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/edit/systeminfo/${AccessToken}/adex/secure`,data)
     enqueueSnackbar('Updated success!');
    } catch (error) {
        if (isMountedRef.current){
            setError('afterSubmit', error);
          }
    }
  };
   
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
     
        <Grid item xs={12} md={8}>
        {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
     
            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >              
                 <RHFTextField name="app_name" label="SYSTEM NAME" defaultValue/>
                 <RHFTextField name="app_phone" label="SYSTEM PHONE NUMBER" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="app_email" label="SYSTEM EMAIL" type="email" defaultValue/>
                 <RHFTextField name="app_address" label="SYSTEM ADDRESS" defaultValue />
                 <RHFTextField name="facebook" label="SYSTEM FACEBOOK URL" defaultValue/>
                 <RHFTextField name="instagram" label="SYSTEM INSTAGRAM URL" defaultValue/>
                 <RHFTextField name="tiktok" label="SYSTEM TIKTOK URL" defaultValue/>
             </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
               Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
